import React from "react";
import { AppBar, Toolbar, Typography, Button, Box } from "@mui/material";
import { Link, useNavigate, useLocation } from "react-router-dom";

export default function Navbar() {
  const location = useLocation();
  const navigate = useNavigate();

  const handleScrollToAbout = () => {
    if (location.pathname === "/") {
      document.getElementById("about-section")?.scrollIntoView({ behavior: "smooth" });
    } else {
      navigate("/"); // Navigo n Home
      setTimeout(() => {
        document.getElementById("about-section")?.scrollIntoView({ behavior: "smooth" });
      }, 100);
    }
  };

  const getButtonStyle = (path) => ({
    fontSize: "20px",
    fontWeight: "bold",
    color: location.pathname === path ? "yellow" : "white",
    textDecoration: "none",
  });

  return (
    <AppBar position="static" color="primary">
      <Toolbar>
        <Typography variant="h6" sx={{ flexGrow: 1, fontWeight: "bold", fontSize: "25px" }}>
          CargoHub-Dauti
        </Typography>
        <Box sx={{ display: "flex", gap: 2 }}>
          <Button color="inherit" component={Link} to="/" sx={getButtonStyle("/")}>
            Home
          </Button>
          <Button color="inherit" onClick={handleScrollToAbout} sx={getButtonStyle("/#about-section")}>
            About
          </Button>
          <Button color="inherit" component={Link} to="/careers" sx={getButtonStyle("/careers")}>
            Careers
          </Button>
          <Button color="inherit" component={Link} to="/contact" sx={getButtonStyle("/contact")}>
            Contact
          </Button>
        </Box>
      </Toolbar>
    </AppBar>
  );
}
